<template>
    <view
            :class="['cl-list', isAppend, isDisabled, isBorder]"
            @touchstart="onTouchStart"
            @touchmove="onTouchMove"
            @touchend="onTouchEnd"
            @tap.stop="onTap"
            :style="{backgroundColor}"
    >
        <view class="cl-list__swiper" :style="{
				transform: translateX
			}">
            <view class="cl-list__container">
                <view class="cl-list__icon" v-if="$slots.icon">
                    <slot name="icon"></slot>
                </view>

                <text class="cl-list__label" v-if="label && label != 'true'">{{ label }}</text>

                <view :class="['cl-list__content', isJustify, isColor]">
                    <slot></slot>
                </view>

                <view class="cl-list__append">
                    <slot name="append"></slot>
                </view>
            </view>

            <!-- 后菜单 -->
            <template v-if="swipe != 'none'">
                <view :class="[`cl-list__menu-${swipe}`]">
                    <slot name="menu"></slot>
                </view>
            </template>
        </view>
    </view>
</template>

<script>
    const { platform } = uni.getSystemInfoSync();

    export default {
        name:'SunnyList',
        props: {
            label: String,
            disabled: Boolean,
            border: { type: Boolean, default: true },
            type: String,
            justify: { type: String, default: "end" },
            backColor:{ type:String, default:'#fff' },
            swipe: {
                type: String,
                default: "none",
                validator: val => {
                    return ["none", "left", "right"].indexOf(val) !== -1;
                }
            }
        },

        data() {
            return {
                touch: {
                    start: 0,
                    end: 0,
                    x: 0,
                    direction: "left",
                    lock: true
                },

                menu: {
                    width: 0
                }
            };
        },

        computed: {
            isColor() { return this.type ? `is-color-${this.type}` : ""; },

            isBorder() { return this.border ? `is-border` : ""; },

            isJustify() { return this.justify !== "start" ? `is-justify-${this.justify}` : ""; },

            isAppend() { return this.$scopedSlots.append ? "cl-list--append" : ""; },

            isDisabled() { return this.disabled ? "cl-list--disabled" : ""; },

            translateX() { return `translateX(${this.touch.x}px)`; },

            backgroundColor(){ return this.backColor }
        },

        watch: {
            swipe() {
                this.setMenu();
            }
        },

        mounted() {
            this.setMenu();
        },

        methods: {
            onTap(e) {
                this.$emit("click", e);
                this.$emit("tap", e);
            },
            onTouchStart(e) {
                if (this.swipe != "none") {
                    this.touch.start = e.touches[0].pageX;
                    this.touch.lock = false;
                }
            },
            onTouchMove(e) {
                const { direction, start, end, lock } = this.touch;

                if (!lock) {
                    let offsetX = e.touches[0].pageX - start;

                    this.touch.direction = offsetX > 0 ? "right" : "left";
                    this.touch.x = end + offsetX;

                    offsetX = null;
                }
            },
            onTouchEnd(e) {
                const { direction, x, end, lock } = this.touch;

                if (!lock) {
                    const maxX =
                        this.menu.width * (this.swipe === "right" ? -1 : 1);

                    if (Math.abs(x - end) > 50) {
                        if (direction === this.swipe) {
                            this.touch.x = 0;
                        } else {
                            this.touch.x = maxX;
                        }

                        this.touch.end = this.touch.x;
                    } else {
                        this.touch.x = end === 0 ? 0 : maxX;
                    }

                    this.touch.lock = true;
                }
            },
            setMenu() {
                if (this.swipe != "none") {
                    const query = uni.createSelectorQuery().in(this);

                    query
                        .select(`.cl-list__menu-${this.swipe}`)
                        .boundingClientRect(data => {
                            if (data) {
                                this.menu.width = data.width;
                            }
                        })
                        .exec();
                }
            },
            restore() {
                this.touch.start = 0;
                this.touch.end = 0;
                this.touch.lock = true;
                this.touch.x = 0;
            }
        }
    };
</script>
<style lang="scss">
.cl-list {
    display: flex;
    align-items: center;
    position: relative;
    overflow: hidden;
    background-color: #fff;

    &--append {
        .cl-list__content {
            margin-right: 20rpx;
        }
    }

    &--disabled {
        background-color: #eee;
        color: #999;

        &:after {
            display: block;
            content: "";
            background-color: rgba(0, 0, 0, 0.1);
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
        }
    }

    &.is-border {
        border-bottom: 1rpx solid #eee;
    }

    &__icon,
    &__label,
    &__append {
        flex-shrink: 0;
    }

    &__label,
    &__content,
    &__append {
        display: flex;
        align-items: center;
        min-height: 80rpx;
        font-size: 26rpx;
    }

    &__swiper {
        display: flex;
        align-items: center;
        transition: all 0.3s;
        width: 100%;
    }

    &__container {
        display: flex;
        align-items: center;
        width: 100%;
        padding: 0 20rpx;
        box-sizing: border-box;

        &:active {
            background-color: #f7f7f7;
        }
    }

    &__icon {
        margin-right: 20rpx;

        img,
        image {
            display: block;
            height: 46rpx;
            width: 46rpx;
        }

        text {
            font-size: 46rpx;
        }
    }

    &__label {
        padding: 0 20rpx 0 0;
        font-size: 28rpx;
    }

    &__content {
        padding: 10rpx 0;
        flex: 1;

        &.is-justify-center {
            justify-content: center;

            picker {
                text-align: center;
            }
        }

        &.is-justify-end {
            justify-content: flex-end;

            picker {
                text-align: end;
            }
        }

        &.is-justify-space-between {
            justify-content: space-between;
        }

        &.is-justify-space-around {
            justify-content: space-around;
        }

        &.is-color-primary {
            color: $color-primary;
        }

        &.is-color-success {
            color: $color-success;
        }

        &.is-color-warning {
            color: $color-warning;
        }

        &.is-color-error {
            color: $color-error;
        }
    }

    &__append {
        i {
            display: inline-block;
        }

        [class^="cl-icon-"] {
            font-size: 30rpx;
        }
    }

    &__menu-left {
        transform: translateX(-100%);
        position: absolute;
        transition: left 0.3s;
    }

    &__menu-right {
        transform: translateX(100%);
        position: absolute;
        right: 0;
        transition: left 0.3s;
    }
}
</style>
